import React, { useEffect, useState } from 'react'
import Axios from 'axios'
import { Field } from 'redux-form';

function FindDocument(props) {

const [date, setdate] = useState(null); 

const [enrollment, setenrollment] = useState(null); 

    const onSubmit =(e) => {
    e.preventDefault(); 
        
        const formData = new FormData();
	var d = new Date(date)
       formData.append("date",d.getFullYear()+'/'+(d.getMonth()+1)+'/'+d.getDate() );
       formData.append("enrollment",enrollment);
     
       (async () => { 
          
        await Axios.post("/admin/findDocument",formData)
          .then((res) => {
            console.log(res.data); 
            
          })
          .catch((err) => console.log(err));
        
      })();

       
        // history.pushState('/login')
      };

    useEffect(() => {
        
       
    }, [])
    return (
        <div>
           
            <h2 className="form-title text-center">ForgotPassword</h2>
            <div className="form-group">
                
           <input type="date" name="date" onChange={e=>setdate(e.target.value)}/>
            </div>
	<div className="form-group">
                
           <input type="text" name="enrollment" onChange={e=>setenrollment(e.target.value)}/>
            </div>

            
            <div className="form-group text-center">
              <button type="button" className="btn btn-success" onClick={e=>onSubmit(e)}>
                Submit
              </button>
            </div>
          
          

        </div>
    )
}

export default FindDocument
